*{
    margin:0;
    padding:0;
    text-decoration: none;
    font-family: "Roboto Mono", monospace;
  font-optical-sizing: auto;
}



body {
    background-color: #000;
}

.container{
    display:flex;
    
}
main{
    width: 100%;
    padding: 20px;
}
.sidebar{
    position: fixed;
    background: linear-gradient(0deg, rgba(12, 12, 12, 0.678) 0%, rgba(20, 20, 20, 0.664) 100%);
   
    color: #fff;
    z-index: 100;
    height: 85VH;
    border-bottom-right-radius: 1em;
    transition: all 0.5s;
    backdrop-filter: blur(4px);
}
.top_section{
    display: flex;
    align-items:center;
    padding:20px 15px;
}
.logo{
    font-size: 30px;
}
.bars{
    display: flex;
    font-size: 25px;
    margin-left: 50px;
}
.link{
    display: flex;
    color: #fff;
    padding: 10px 15px;
    gap: 15px;
    transition: all 0.5s;
}
.link:hover{
    background:orange;
    color: #000;
    transition: all 0.5s;
}
.active{
    background: white;
    color: #000;
}
.icon, .link_text{
    font-size: 20px;
}


.TopbarMaster {
    position: fixed;
    top: 0%;
    bottom: 100%;
    height: 4em;
    width: 100%;
    margin-bottom: 2em;
    border-bottom: 1px solid white;
}

.topbarMain {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    color: white;
   margin-left: 1em;
}

.idealLogo {
    width: auto;
    height: 4em;

}

.menuItem {
    width: 15%;
    height: 50%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    align-content: center;
    cursor: pointer;
    transition: all 0.5s;
}

.menuItem:hover {
    background-color: orange;
    color: #000;
    font-size: 1.1em;
    font-weight: 900;
    height: 80%;
}


.idealLogo { 
    width: 80%;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    align-content: center;
}

.Logo1 {
    height: 95%;
    margin-left: 2em;
    transition: all 0.5s;
}

.Logo2 {
    height: 100%;
    width:50%;
    transition: all 0.5s;
}

.socialsIcons {
    width: 50%;
    height: 70%;
}




@media screen and (max-width: 700px) {
    .idealLogo {
        width: 100%;
    }
    .menuItem {
        display: none;
    }
}